import React, { Component } from "react";
import "../assets/css/external.css";
import Footer from "../components/footer/footer";
import { AuthConsumer } from "../components/authcontext";
import NavBarMobile from "../components/navbar/navBarMobile";
import NavBarAuthenticated from "../components/navbar/navBarAuthenticated";


class PrivacyPage extends Component {
  render() {
    return (
      <>
        <AuthConsumer>
          {({ logout }) => (
            <>
              <NavBarAuthenticated logout={logout} />
              <NavBarMobile logout={logout} />
            </>
          )}
        </AuthConsumer>
      <div className="external-content">
        <div className="page-title">
          <h1 className="text-center">Privacy Policy</h1>
        </div>
        <div className="body-text container">
          <p>
            This Privacy Policy was last modified on December 7, 2011, and was first published and made effective on December 7, 2011. Bloc, Inc. (“Bloc,” “we,” or “us”) provides this Privacy Policy to explain our practices regarding the collection, use, disclosure, and protection of information that apply to our Service, as well as your choices regarding the collection and use of information. This Privacy Policy does not apply to information we collect by means (including offline) or from other sources other than through the Service. Capitalized terms that are not defined in this Privacy Policy have the meaning given them in our Terms of Use.
          </p>
          <br />
          <p>
            1. HOW WE COLLECT AND USE INFORMATION
          </p>
          <p>
            Personally Identifiable Information: We collect information that can be used to identify you as an individual (“Personally Identifiable Information”) only when you provide such information directly to us in connection with the Service.
          </p>
            <br />
          <p>
            We ask for Personally Identifiable Information such as your name and e-mail address when you register for a Member account with the Service, or if you correspond with us (in which case we will also retain our responses). We may also collect demographic information, such as your age, gender, preferences, and interests. If you purchase products or services or sign up for a paid subscription, then we will also ask for additional information, such as your credit card number and billing address. We may also retain any messages you send through the Service, and may collect information you provide in User Content you post to the Service.
          </p>
          <br />
          <p>
            We may receive Personally Identifiable Information about you from third parties, including, for example, information about your transactions, purchase history, or relationships with various product and service providers, and your use of certain applications. For example, if you access our website or Service through a third-party connection or log-in, for example, through Facebook Connect, by “following,” “liking,” adding the Bloc application, linking your account to the Bloc service, etc., that third party may pass certain information about your use of its service to Bloc. This information could include, but is not limited to, the user ID associated with your account, any information you have permitted the third party to share with us, and any information you have made public in connection with that service. You should always review, and if necessary, adjust your privacy settings on third-party websites and services before linking or connecting them to Bloc’s website or Service.
          </p>
          <br />
          <p>
            We use Personally Identifiable Information to communicate directly with you. We may send you emails containing newsletters, promotions and special offers. If you do not want to receive these marketing communications, you may change your preferences in your user profile settings page. We also use your Personally Identifiable Information to send you Service-related emails (e.g., account verification, purchase and billing confirmations and reminders, changes/updates to features of the Service, technical and security notices). You may not opt out of Service-related e-mails.
          </p>
          <br />
          <p>
            If you choose to use our invitation service to invite a third party to the Service, we will ask you for that person’s email address and automatically send an email invitation to the invitee. Bloc stores this information to send this email, to register the invitee if your invitation is accepted, and to track the success of our invitation service. Unless the invitee becomes a registered user of the Service, Bloc does not use the invitee’s email address for marketing purposes, and does not share the invitee’s email address with third parties other than Bloc’s service providers or in the event of a corporate transaction or proceeding as described below in Sharing of Your Information. The invitee may contact us at any time to request that we remove his or her information from our database at privacy@bloc.io.
          </p>
          <br />
          <p>
            Non-Personally Identifiable Information: We also collect and use information about your interactions with the Service in a manner and format that does not identify you as an individual (“Non-Personally Identifiable Information”). Generally, we do not combine or associate Non-Personally Identifiable Information with your Personally Identifiable Information. If we do combine Non-Personally Identifiable Information with Personally Identifiable Information, we treat the combined information as Personally Identifiable Information. We may collect, use, and disclose Non-Personally Identifiable Information as set forth below.
          </p>
          <br />
          <p>
            Analytics Information: We use third-party analytics tools to help us measure traffic and usage trends for the Service. These tools collect information sent by your browser as part of a web page request, including the web pages you visit, your browser add-ons, your browser’s width and height, and other information that assists us in improving the Service. We collect and use this analytics information in aggregate form such that it cannot reasonably be manipulated to identify any particular individual user.
          </p>
          <br />
          <p>
            Cookies Information: When you visit the Service, we may send one or more cookies — a small text file containing a string of alphanumeric characters — to your computer that uniquely identifies your browser and lets Bloc help you log in faster and enhance your navigation through the site. A cookie may also convey information to us about how you use the Service (e.g., the pages you view, the links you click and other actions you take on the Service), and allow us or our business partners to track your usage of the Service over time. A persistent cookie remains on your hard drive after you close your browser. Persistent cookies may be used by your browser on subsequent visits to the site. Persistent cookies can be removed by following your web browser’s directions. A session cookie is temporary and disappears after you close your browser. You can reset your web browser to refuse all cookies or to indicate when a cookie is being sent. However, some features of the Service may not function properly if the ability to accept cookies is disabled.
          </p>
          <br />
          <p>
            We may share your Personally Identifiable Information with third-party business partners for the purpose of providing the Service to you. Those business partners will be given limited access to the Personally Identifiable Information that is reasonably necessary to deliver the Service, and we will require that such third parties comply with this Privacy Policy.
          </p>
          <br />
          <p>
            change the policy, we will update the ‘last modified’ date at the top of this page. If there are material changes to this Privacy Policy or in how Bloc will use your personally identifiable information, we will notify you either by prominently posting a notice of such changes prior to implementing the change or by directly sending you a notification.
          </p>
          </div>
        </div>
        <Footer />
        </>
    );
  }
}

export default PrivacyPage;
